import React from 'react';
import { isMobile } from 'react-device-detect';
// Correct pour les versions rcentes
import { QRCodeCanvas } from 'qrcode.react';

const DownloadApp = () => {
  const apkLink = 'https://assistbyscan.com/app-debug.apk';

  const handleMobileClick = () => {
    window.location.href = apkLink; // Tlchargement direct sur mobile
  };

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      minHeight: '100vh',
      fontFamily: 'Arial',
      textAlign: 'center',
      padding: '20px'
    }}>
      <h2>Install Assist by Scan</h2>

      {isMobile ? (
        <>
          <p>Tap the button below to download the app:</p>
          <button
            onClick={handleMobileClick}
            style={{
              padding: '12px 24px',
              fontSize: '16px',
              borderRadius: '8px',
              backgroundColor: '#007bff',
              color: '#fff',
              border: 'none',
              cursor: 'pointer'
            }}
          >
            Download APK
          </button>
        </>
      ) : (
        <>
          <p>Scan this QR code with your Android device to download the app:</p>
          <QRCodeCanvas value="https://assistbyscan.com/app-debug.apk" size={250} />
          <div style={{ marginTop: '10px', fontStyle: 'italic' }}>Assist by scan</div>
        </>
      )}
    </div>
  );
};

export default DownloadApp;
